import axios from "axios";
export default {
  data: () => ({
    groups: [],
    show: false,
    hover: false
  }),

  created() {
    axios.get(this.$store.state.apiURL + "/group/user/" + this.$store.state.user._id, {
      headers: {
        token: localStorage.getItem("token")
      }
    }).then(res => {
      this.groups = res.data;
    });
  }

};